import clsx from 'clsx'
import ActionButton from 'components/Dashboard/ActionButton'
import InviteFriends from '../InviteFriends'
import { AntSwitch, useStyles } from './Style'
import leftArrow from '../../../../assets/image/leftarrow_icon.svg'
import { useEffect, useState } from 'react'
import { FormControlLabel } from '@material-ui/core'
import { useAppDispatch, useAppSelector } from 'store/hooks'
import { headerPrices } from 'store/headerinfo/selectors'
import { CoinBalance } from 'hooks/orderFormAxios'
import { getHeaderPrices } from 'store/headerinfo'
import jwt_decode from 'jwt-decode'

interface MyToken {
  userId: string
}

export default function BalanceDetail() {
  const classes = useStyles()
  const dispatch = useAppDispatch()
  const topTabHeader = ['Spot', 'P2P', 'Margin', 'Futures', 'WazirX', 'Pool']
  const [seletedTab, setSeletedTab] = useState<string>(topTabHeader[0])
  const [mobileSide, setMobileSide] = useState(false)
  const [switchStatus, setSwitchStatus] = useState(false)
  const headerInfos = useAppSelector(headerPrices)
  const [coin1Balance, setCoin1Balance] = useState(0)

  let decoded: any = []
  if (localStorage.jwtToken) {
    decoded = jwt_decode<MyToken>(localStorage.jwtToken)
  }

  const getCoin1Balance = async () => {
    const formData = new FormData()
    formData.append('user_id', decoded?.userId)
    formData.append('symbol', 'PLATYPUS')

    CoinBalance(formData).then((res: any) => {
      setCoin1Balance(res?.data?.Balance)
    })
  }

  useEffect(() => {
    getCoin1Balance()
    const formData = new FormData()
    formData.append('pair', 'PLATYPUS/USDT')
    dispatch(getHeaderPrices(formData))
    //eslint-disable-next-line react-hooks/exhaustive-deps
  }, [decoded.userId])

  const handleTab = (e: string) => {
    setSeletedTab(e)
    setMobileSide(false)
  }

  const handleMobileSidebar = () => {
    setMobileSide(!mobileSide)
  }

  const handleSwitch = () => {
    setSwitchStatus(!switchStatus)
  }

  return (
    <div className={classes.root}>
      <div className={classes.header}>
        <div className={classes.title}>Balance Details</div>
        <div className={classes.btnsRoot}>
          <ActionButton type="normal" onClick={() => {}} className={classes.btn}>
            Deposit
          </ActionButton>
          <ActionButton type="normal" onClick={() => {}} className={classes.btn}>
            Withdraw
          </ActionButton>
          <ActionButton type="normal" onClick={() => {}} className={classes.btn}>
            Gift Card
          </ActionButton>
          <ActionButton type="normal" onClick={() => {}} className={clsx(classes.btn, classes.lastBtn)}>
            <img src={leftArrow} alt="leftarrowicon" />
          </ActionButton>
        </div>
        <InviteFriends className={classes.inviteRoot} />
      </div>
      <div className={classes.content}>
        <div className={classes.topTabRoot}>
          {topTabHeader?.map((key: any) => {
            return (
              <div
                className={key === seletedTab ? classes.activeTopTabItem : classes.topTabItem}
                onClick={() => handleTab(key)}
              >
                {key}
              </div>
            )
          })}
        </div>
        <div className={classes.contentRoot}>
          <div className={classes.sidebar}>
            <div className={classes.menuIconRoot}>
              <div className={classes.menuIcon} onClick={handleMobileSidebar}>
                <i className="fas fa-bars"></i>
              </div>
            </div>
            <div className={!mobileSide ? classes.displayNone : classes.mobileSidebarItemRoot}>
              {topTabHeader?.map((key: any) => {
                return (
                  <div
                    className={key === seletedTab ? classes.activeMobileTabItem : classes.topTabItem}
                    onClick={() => handleTab(key)}
                  >
                    {key}
                  </div>
                )
              })}
            </div>
          </div>
          <div className={classes.mainContent}>
            <div className={classes.showRoot}>
              <div className={classes.cards}>
                <div className={classes.cardRoot}>
                  <div className={classes.cardTitle}>Account Balance</div>
                  <div className={classes.cardValue}>{switchStatus ? '****' : coin1Balance?.toFixed(2)} PLATYPUS</div>
                  <div className={classes.cardEffect}></div>
                </div>
                <div className={clsx(classes.cardRoot, classes.lastCard)}>
                  <div className={classes.cardTitle}>Estimated Value</div>
                  <div className={classes.cardValue}>
                    {switchStatus ? '****' : '$' + (coin1Balance * headerInfos.Price).toFixed(2)}
                  </div>
                  <div className={classes.cardEffect}></div>
                </div>
              </div>
              <div className={classes.switchRoot}>
                <FormControlLabel
                  className={classes.switchForm}
                  control={
                    <AntSwitch
                      checkedIcon={<i className={clsx('fas fa-eye', classes.switchIcon)}></i>}
                      icon={<i className={clsx('fas fa-eye-slash', classes.switchIcon)}></i>}
                      checked={switchStatus}
                      onClick={handleSwitch}
                      disableRipple
                    />
                  }
                  label={`${!switchStatus ? `Hide` : `Show`} Balance`}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
