import React, { useEffect, useState } from 'react'
import clsx from 'clsx'
import { Box, Typography } from '@material-ui/core'
import P2P from './P2P'
import BuyCrypto from './BuyCrypto'
import Withdraw from './Withdraw'
import { useStyles } from './style'
import ActionButton from 'components/Dashboard/ActionButton'
import AddBankAccountLockedModal from 'components/Dashboard/AddBankAccountLockedModal'
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline'
import { useLocation, useHistory } from 'react-router-dom'
import { myInfoTab } from 'config/constants'

function Payment() {
  const classes = useStyles()
  const [activeItem, setActiveItem] = useState(myInfoTab.p2p)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const location = useLocation()
  const history = useHistory()

  const query = new URLSearchParams(location.search)

  const setTab = (search: string) => {
    query.set('tab', search)
    history.push({
      pathname: location.pathname,
      search: query.toString(),
    })
  }

  useEffect(() => {
    if (query.get('tab') === null) {
      setActiveItem(myInfoTab.p2p)
      return
    }
    if (query.get('tab') === myInfoTab.p2p) {
      setActiveItem(myInfoTab.p2p)
    } else if (query.get('tab') === myInfoTab.buy) {
      setActiveItem(myInfoTab.buy)
    } else {
      setActiveItem(myInfoTab.withdraw)
    }
    //eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location])

  return (
    <Box className={classes.container}>
      <Box className={classes.paymentHeader}>
        <Typography className={classes.pageTitle}>Payment</Typography>
        {activeItem === myInfoTab.withdraw ? (
          <ActionButton type="yellow" onClick={() => setIsModalOpen(true)}>
            <AddCircleOutlineIcon style={{ fontSize: '20px', marginRight: '8px' }} />
            Add Bank Account
          </ActionButton>
        ) : (
          <></>
        )}
      </Box>
      <Box width="100%">
        <Box className={classes.tabContainer}>
          <Box
            className={clsx(classes.tabItem, { [classes.activeTabItem]: activeItem === myInfoTab.p2p })}
            onClick={() => setTab(myInfoTab.p2p)}
          >
            <Typography className={classes.tabItemName}>P2P</Typography>
          </Box>
          <Box
            className={clsx(classes.tabItem, { [classes.activeTabItem]: activeItem === myInfoTab.buy })}
            onClick={() => setTab(myInfoTab.buy)}
          >
            <Typography className={classes.tabItemName}>Buy Crypto</Typography>
          </Box>
          <Box
            className={clsx(classes.tabItem, { [classes.activeTabItem]: activeItem === myInfoTab.withdraw })}
            onClick={() => setTab(myInfoTab.withdraw)}
          >
            <Typography className={classes.tabItemName}>Withdraw</Typography>
          </Box>
        </Box>
        <Box>
          {activeItem === myInfoTab.p2p && <P2P />}
          {activeItem === myInfoTab.buy && <BuyCrypto />}
          {activeItem === myInfoTab.withdraw && <Withdraw />}
        </Box>
      </Box>
      <AddBankAccountLockedModal open={isModalOpen} onClose={() => setIsModalOpen(false)} />
    </Box>
  )
}

export default Payment
