//material-ui
import { Switch } from '@material-ui/core'
import { createStyles, makeStyles, Theme, withStyles } from '@material-ui/core/styles'
//style

export const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {},
    header: {
      padding: '62px 40px 30px 40px',
      display: 'flex',
      alignItems: 'center',
      [theme.breakpoints.down(1180)]: {
        flexDirection: 'column',
        justifyContent: 'center',
      },
    },
    title: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 700,
      fontSize: '22px',
      lineHeight: '26px',
      color: '#FFFFFF',
      opacity: 0.9,
      flexGrow: 1,
      [theme.breakpoints.down(1180)]: {
        marginBottom: '50px',
        fontSize: '30px',
      },
    },
    btnsRoot: {
      display: 'flex',
      alignItems: 'center',
      [theme.breakpoints.down(1180)]: {
        marginBottom: '20px',
      },
    },
    btn: {
      marginRight: '19px',
    },
    lastBtn: {
      marginRight: '25px',
      [theme.breakpoints.down(1180)]: {
        marginRight: 'unset',
      },
      '& img': {
        transform: 'rotate(180deg)',
      },
    },
    inviteRoot: {
      width: '500px',
      [theme.breakpoints.down(1366)]: {
        width: 'unset',
      },
    },
    content: {
      [theme.breakpoints.down(960)]: {
        borderTop: '1px solid #333353',
      },
    },
    topTabRoot: {
      width: '100%',
      display: 'flex',
      alignItems: 'center',
      background: '#12122E',
      justifyContent: 'space-between',
      [theme.breakpoints.down(960)]: {
        display: 'none',
      },
    },
    topTabItem: {
      padding: '15px 29px',
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '20px',
      lineHeight: '23px',
      textAlign: 'center',
      color: '#c8c8cf',
      cursor: 'pointer',
      '&:hover': {
        color: '#ffffff',
      },
    },
    menuIconRoot: {
      borderRight: '1px solid #333353',
    },
    menuIcon: {
      color: '#FFFFFF',
      padding: '10px',
      '& i': {
        cursor: 'pointer',
      },
    },
    activeTopTabItem: {
      padding: '15px 29px',
      borderBottom: '2px solid #F0BA03',
      boxSizing: 'border-box',
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '20px',
      lineHeight: '23px',
      textAlign: 'center',
      color: '#FFFFFF',
    },
    activeMobileTabItem: {
      padding: '0px 20px',
      margin: '15px 9px',
      borderRight: '2px solid #F0BA03',
      boxSizing: 'border-box',
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '20px',
      lineHeight: '23px',
      textAlign: 'center',
      color: '#FFFFFF',
    },
    contentRoot: {
      display: 'flex',
    },
    sidebar: {
      background: '#12122E',
      display: 'flex',
      [theme.breakpoints.up(960)]: {
        display: 'none',
      },
    },
    mobileSidebarItemRoot: {
      background: '#12122E',
    },
    mainContent: {
      width: '100%',
      padding: '50px 0px 67px 0px',
    },
    showRoot: {
      width: '977px',
      margin: 'auto',
      [theme.breakpoints.down(1366)]: {
        width: 'calc( 100% - 80px)',
      },
    },
    cards: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      [theme.breakpoints.down(900)]: {
        flexDirection: 'column',
      },
    },
    cardRoot: {
      padding: '35px 36px',
      background: '#202037',
      // boxShadow: '0px 0px 27px rgba(41, 182, 246, 0.05)',
      borderRadius: '6px',
      position: 'relative',
      textAlign: 'center',
      overflow: 'hidden',
      width: '428px',
      [theme.breakpoints.down(1240)]: {
        width: '100%',
      },
    },
    cardTitle: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 700,
      fontSize: '22px',
      lineHeight: '26px',
      textAlign: 'center',
      color: '#FFFFFF',
      opacity: 0.9,
      marginBottom: '27px',
    },
    cardValue: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '42px',
      lineHeight: '49px',
      textAlign: 'center',
      color: '#FFFFFF',
    },
    cardEffect: {
      position: 'absolute',
      left: '58%',
      top: '68%',
      background: '#FFFFFF',
      width: '367px',
      height: '220px',
      opacity: 0.05,
      boxShadow: '0px 0px 27px rgba(41, 182, 246, 0.15)',
      borderRadius: '6px',
      transform: 'matrix(0.71, -0.49, 1.02, 0.71, 0, 0)',
    },
    displayNone: {
      display: 'none',
    },
    lastCard: {
      marginLeft: '121px',
      [theme.breakpoints.down(1366)]: {
        marginLeft: '20px',
      },
      [theme.breakpoints.down(900)]: {
        marginLeft: 'unset',
        marginTop: '20px',
      },
    },
    switchRoot: {
      paddingTop: '30px',
      paddingLeft: '135px',
      [theme.breakpoints.down(550)]: {
        paddingLeft: '50px',
        paddingTop: '20px',
      },
    },
    switchForm: {
      '& .MuiTypography-body1': {
        fontFamily: 'Roboto',
        fontStyle: 'normal',
        fontWeight: 500,
        fontSize: '18px',
        lineHeight: '25px',
        color: '#6B6B7A',
        marginLeft: '8px',
      },
    },
    switchIcon: {
      fontSize: '10px',
      lineHeight: '10px',
      color: '#080821',
    },
  }),
)

export const AntSwitch = withStyles((theme: Theme) =>
  createStyles({
    root: {
      width: 40,
      height: 21,
      padding: 0,
      display: 'flex',
      '& .MuiIconButton-root:hover': {
        backgroundColor: '#b4b4bc ',
      },
      '& .MuiSwitch-colorSecondary.Mui-checked:hover': {
        backgroundColor: '#525263',
      },
    },
    switchBase: {
      padding: 5,
      background: '#b4b4bc ',
      '&$checked': {
        transform: 'translateX(18px)',
        background: '#525263',
        '& + $track': {
          opacity: 1,
          backgroundColor: '#1a1a31', //white opacity 0.1
          borderColor: theme.palette.primary.main,
        },
      },
    },
    thumb: {
      width: 17,
      height: 17,
      boxShadow: 'none',
    },
    track: {
      borderRadius: '10.5px',
      background: '#404052', //white opactiy 0.3
      opacity: 1,
    },
    checked: {},
  }),
)(Switch)
