//material-ui
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles'
//style
export const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    labelNav: {
      display: 'flex',
      alignItems: 'center',
      fontFamily: 'Roboto',
      '& i': {
        margin: '0px 13px 0px 24px',
        color: '#848e9c',
        fontSize: '24px',
      },
    },
    arrowIcon: {
      '& svg': {
        margin: '0px 4px',
        color: '#848e9c',
        fontSize: '20px',
      },
    },
    sidebarNav: {
      minHeight: 'calc(100vh - 81px)',
      display: 'flex',
      justifyContent: 'center',
      top: 64,
      boxShadow: 'rgb(0 0 0 / 8%) 0px 2px 4px, rgb(0 0 0 / 8%) 0px 0px 4px',
      minWidth: '272px',
      padding: '15px 12px 0px 12px',
      borderRight: '1px solid #333353',
      zIndex: 1,
      [theme.breakpoints.down('md')]: {
        minWidth: '272px',
      },
      [theme.breakpoints.down(768)]: {
        display: 'none',
      },
    },
    sidebarWrap: {
      width: '100%',
      background: '#12122E',
      borderRadius: '5px',
      height: 'fit-content',
    },
    sidebarLinksContainer: {
      borderRadius: '5px',
      backgroundColor: '#12122E',
      padding: '27px 32px',
    },
    sidebarLink: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      listStyle: 'none',
      height: '65px',
      textDecoration: 'none',
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '18px',
      lineHeight: '21px',
      color: '#C7C7C8',
      background: '#12122E',
      borderTop: '1px solid #333353',
      '&:hover': {
        background: 'rgba(255, 255, 255, 0.07)',
        cursor: 'pointer',
        color: '#ffffff',
      },
    },
    sidebarLinkWithout: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      listStyle: 'none',
      height: '65px',
      textDecoration: 'none',
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '18px',
      lineHeight: '21px',
      color: '#C7C7C8',
      borderTop: '1px solid transparent',
      background: '#12122E',
      '&:hover': {
        background: 'rgba(255, 255, 255, 0.07)',
        cursor: 'pointer',
        color: '#ffffff',
      },
    },
    activeSidebarLink: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      listStyle: 'none',
      height: '65px',
      textDecoration: 'none',
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '18px',
      lineHeight: '21px',
      color: '#FFFFFF',
      background: 'rgba(255, 255, 255, 0.07)',
      '&:hover': {
        background: 'rgba(255, 255, 255, 0.07)',
        cursor: 'pointer',
        color: '#ffffff',
      },
    },
    divLine: {
      width: '3px',
      height: '35px',
      marginRight: '15px',
      background: 'transparent',
    },
    activeDivLine: {
      width: '3px',
      height: '35px',
      marginRight: '15px',
      background: '#F0BA03',
    },
    dropdownLinkRoot: {
      borderTop: '1px solid #333353',
      height: '64px',
      padding: '15px 10px',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      textDecoration: 'none',
      fontFamily: 'Roboto',
      fontWeight: 500,
      lineHeight: '21.09px',
      color: '#A3A3A9',
      fontSize: '18px',
      backgroundColor: '#12122E',
      '&:hover': {
        background: 'rgb(255 255 255 / 7%)',
        cursor: 'pointer',
        color: 'white',
      },
    },
    dropdownLinkContainer: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      padding: '7px',
      textAlign: 'center',
      borderLeft: '1px solid transparent',
    },
    activeDropdownLinkRoot: {
      height: '64px',
      borderTop: '1px solid #333353',

      padding: '15px 10px',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',

      textDecoration: 'none',
      fontFamily: 'Roboto',
      color: 'white',
      fontSize: '18px',
      fontWeight: 500,
      background: 'rgb(255 255 255 / 7%)',
      '&:hover': {
        cursor: 'pointer',
        color: 'white',
      },
    },
    activeDropdownLinkContainer: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      width: '100%',
      height: '100%',
      borderRight: '3px solid #F0BA03',
    },
  }),
)
