//material-ui
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles'
//style
export const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    cardRoot: {
      background: '#080821',
      border: '1px solid #333353',
      borderRadius: '6px',
      width: '100%',
      boxShadow: 'rgb(20 21 26 / 4%) 0px 1px 2px, rgb (71 77 87 / 4%) 0px 3px 6px, rgb(20 21 26 / 10%) 0px 0px 1px',
      boxSizing: 'border-box',
    },
    cardHeader: {
      display: 'flex',
      padding: '15px 20px 23px 20px',
      justifyContent: 'space-between',
      alignItems: 'center',
      background: '#12122E',
      borderRadius: '6px 6px 0 0',
    },
    headerTitle: {
      display: 'flex',
      boxSizing: 'border-box',
      minWidth: '0px',
      padding: '0px',
      alignItems: 'center',
    },
    titleName: {
      textDecoration: 'none',
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 600,
      fontSize: '20px',
      lineHeight: '23px',
      textAlign: 'center',
      color: '#FFFFFF',
      '&:hover': {
        color: '#F0BA03',
      },
    },
    titleMore: {
      display: 'flex',
      marginLeft: '10px',
    },
    endPart: {
      flexGrow: 1,
    },
    headerDescription: {
      display: 'flex',
      justifyContent: 'end',
      alignItems: 'center',
    },
    descriptionMore: {
      display: 'flex',
      marginRight: '10px',
      flexGrow: 1,
      justifyContent: 'end',
      alignItems: 'center',
    },
    descriptionIcon: {
      display: 'flex',
      color: '#ffffff',
      width: '10px',
      height: '12px',
      cursor: 'pointer',
      '&:hover': {
        color: '#f1bb12',
      },
    },
  }),
)
