import Card from '@material-ui/core/Card'
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos'
import { Link } from 'react-router-dom'
//----------------style-----------
import { useStyles } from './Style'
import clsx from 'clsx'

interface CustomCardProps {
  title?: string
  endDesc?: React.ReactNode
  frontDesc?: React.ReactNode
  url: string
  children?: React.ReactNode
  unlink?: boolean | false
  className?: any
}
function CustomCard({ title, endDesc, frontDesc, url, children, unlink, className }: CustomCardProps) {
  const classes = useStyles()

  return (
    <Card className={clsx(classes.cardRoot, className)}>
      {!unlink && (
        <div className={classes.cardHeader}>
          <div className={classes.headerTitle}>
            <Link to={url} className={classes.titleName}>
              {title}
            </Link>
            <div className={classes.titleMore}>{frontDesc}</div>
          </div>
          <div className={classes.endPart}>
            <div className={classes.headerDescription}>
              <div className={classes.descriptionMore}>{endDesc}</div>
              <Link to={url}>
                <ArrowForwardIosIcon className={classes.descriptionIcon} />
              </Link>
            </div>
          </div>
        </div>
      )}
      <div>{children}</div>
    </Card>
  )
}
export default CustomCard
