import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import clsx from 'clsx'
//material-ui
import { createStyles, Theme, withStyles, WithStyles } from '@material-ui/core/styles'
import { Grid, Typography, IconButton, Dialog, Box, Divider } from '@material-ui/core'
import MuiDialogTitle from '@material-ui/core/DialogTitle'
import MuiDialogContent from '@material-ui/core/DialogContent'
import CloseIcon from '@material-ui/icons/Close'
//external
import { useStyles } from './Style'
import SpotTradingLightIcon from '../../../assets/image/SpotTradingLightIcon.svg'
import SpotTradingDarkIcon from '../../../assets/image/SpotTradingDarkIcon.svg'
import HotTopicLightIcon from '../../../assets/image/HotTopicLightIcon.svg'
import HotTopicDarkIcon from '../../../assets/image/HotTopicDarkIcon.svg'
import TutorialVideoLightIcon from '../../../assets/image/TutorialVideoLightIcon.svg'
import TutorialVideoDarkIcon from '../../../assets/image/TutorialVideoDarkIcon.svg'
// import TutorialVideoModal from './TutorialVideoModal'
import TutorialVideoModal from 'components/TutorialVideoModal'
import RoundBackBtn from 'components/Base/RoundBackBtn'
import GreyDot from '../../../assets/image/greyDot.png'
import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt'

const styles = (theme: Theme) =>
  createStyles({
    root: {
      margin: 0,
      padding: '27px 48px 29px 27px',
      borderBottom: '1px solid #333353',
    },
    closeButton: {
      position: 'absolute',
      right: '30px',
      top: '20px',
      width: '35px',
      height: '35px',
      color: theme.palette.grey[500],
    },
  })

export interface DialogTitleProps extends WithStyles<typeof styles> {
  id: string
  children: React.ReactNode
  onClose: () => void
}

const DialogTitle = withStyles(styles)((props: DialogTitleProps) => {
  const { children, classes, onClose, ...other } = props
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        // <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
        //   <CloseIcon />
        // </IconButton>
        <RoundBackBtn type="multiply" className={classes.closeButton} onClick={onClose} />
      ) : null}
    </MuiDialogTitle>
  )
})

const DialogContent = withStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(3),
  },
}))(MuiDialogContent)

interface HelpDialogProps {
  open: boolean
  handleClose: () => void
}

export default function HelpDialog({ open, handleClose }: HelpDialogProps) {
  const classes = useStyles()

  const [openVideoModal, setOpenVideoModal] = useState(false)

  const handleOpenVideoModal = (e: boolean) => {
    setOpenVideoModal(e)
  }

  return (
    <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open} className={classes.dialog}>
      <TutorialVideoModal
        modalopen={openVideoModal}
        handleClose={() => setOpenVideoModal(false)}
        // handleClickNumber={handleGetPhoneNumber}
      />
      <DialogTitle id="customized-dialog-title" onClose={handleClose}>
        <Typography className={classes.helpmodalTitle}>Spot Help Center</Typography>
      </DialogTitle>

      <DialogContent className={classes.dioContentRoot}>
        <Grid container>
          <Grid item xs={12}>
            <Box display="flex" className={classes.smallTitleContainer}>
              <img
                src={localStorage.appTheme === 'darkTheme' ? HotTopicLightIcon : HotTopicDarkIcon}
                alt="icon"
                className={classes.hotTopicIcon}
              />
              <span className={classes.smallTitle}>Hot Topics</span>
            </Box>

            <Box ml="67px" mt="24px">
              <Link to="/support/faq/s001" style={{ textDecoration: 'none' }} className={classes.hotTopicDesContainer}>
                <img src={GreyDot} className={classes.dot} />
                <Typography className={classes.hotTopicDes}>How to Register on Platypus by Email</Typography>
              </Link>

              <Link to="/support/faq/s002" style={{ textDecoration: 'none' }} className={classes.hotTopicDesContainer}>
                <img src={GreyDot} className={classes.dot} />
                <Typography className={classes.hotTopicDes}>How to Login on Platypus web</Typography>
              </Link>

              <Link to="/support/faq/s003" style={{ textDecoration: 'none' }} className={classes.hotTopicDesContainer}>
                <img src={GreyDot} className={classes.dot} />
                <Typography className={classes.hotTopicDes}>I forgot my password from Platypus account</Typography>
              </Link>

              <Link to="/support/faq/s004" style={{ textDecoration: 'none' }} className={classes.hotTopicDesContainer}>
                <img src={GreyDot} className={classes.dot} />
                <Typography className={classes.hotTopicDes}>How to Trade Spot on Platypus Website</Typography>
              </Link>

              <Link to="/support/faq/s005" style={{ textDecoration: 'none' }} className={classes.hotTopicDesContainer}>
                <img src={GreyDot} className={classes.dot} />
                <Typography className={classes.hotTopicDes}>How to Deposit Fiat to Platypus</Typography>
              </Link>

              <Link to="/support/faq/s006" style={{ textDecoration: 'none' }} className={classes.hotTopicDesContainer}>
                <img src={GreyDot} className={classes.dot} />
                <Typography className={classes.hotTopicDes}>How to Deposit Crypto to Platypus</Typography>
              </Link>

              <Link to="/faq" style={{ textDecoration: 'none' }}>
                <Typography variant="body2" className={clsx(classes.learnMore)}>
                  Learn more <ArrowRightAltIcon />
                </Typography>
              </Link>
            </Box>
          </Grid>
        </Grid>

        <Divider className={classes.divedeLine} />

        <Grid container spacing={3}>
          <Grid item xs={6}>
            <Box className={classes.smallTitleContainer}>
              <img
                src={localStorage.appTheme === 'darkTheme' ? SpotTradingLightIcon : SpotTradingDarkIcon}
                alt="icon"
                className={classes.hotTopicIcon}
                style={{ marginBottom: '2px' }}
              />
              <span className={classes.smallTitle}>Spot Trading Rules</span>
            </Box>

            <div className={classes.downPartDescLetter} style={{ marginLeft: '32px' }}>
              Learn about trading rules for each pair
            </div>
          </Grid>

          <Grid item xs={6} className={classes.videoClick}>
            <Box className={classes.smallTitleContainer} onClick={() => handleOpenVideoModal(true)}>
              <img
                src={localStorage.appTheme === 'darkTheme' ? TutorialVideoLightIcon : TutorialVideoDarkIcon}
                alt="icon"
                className={classes.hotTopicIcon}
                style={{ marginBottom: '2px' }}
              />
              <span className={classes.smallTitle}>Tutorial Video</span>
            </Box>

            <div className={classes.downPartDescLetter} style={{ marginLeft: '32px' }}>
              Spend 3 mins to know spot trading by watching video
            </div>
          </Grid>
        </Grid>
      </DialogContent>
    </Dialog>
  )
}
