import { useEffect, useState } from 'react'
import { Box, Grid } from '@material-ui/core'
import { useStyles } from './style'
import jwt_decode from 'jwt-decode'
import { GetAffiliateBalance } from 'hooks/dashboard'
import { useAppDispatch, useAppSelector } from 'store/hooks'
import { getHeaderPrices } from 'store/headerinfo'
import { headerPrices } from 'store/headerinfo/selectors'
import { GetPromoterLink } from 'hooks/dashboard'

import copy from 'copy-text-to-clipboard'
import MyAffiliates from './MyAffiliates'
import Youtube from './Youtube'

interface MyToken {
  userId: string
}

function Affiliate() {
  const classes = useStyles()

  const dispatch = useAppDispatch()
  const headerInfos = useAppSelector(headerPrices)

  const [balance, setBalance] = useState(0)

  let decoded: any = []
  if (localStorage.jwtToken) {
    decoded = jwt_decode<MyToken>(localStorage.jwtToken)
  }

  useEffect(() => {
    const formData = new FormData()
    formData.append('user_id', decoded?.userId)

    GetAffiliateBalance(formData).then((res: any) => {
      setBalance(res?.data?.Balance)
    })
  }, [decoded?.userId])

  useEffect(() => {
    const formData = new FormData()
    formData.append('pair', 'PLATYPUS/USDT')
    dispatch(getHeaderPrices(formData))
  }, [dispatch])

  //////////////////////////////////////////////////////////////////////////////////////////////////
  const [copyStatus, setCopyStatus] = useState(false)
  const [linkValue, setLinkValue] = useState('https://app.platypustrade.com/register')

  const handleCopy = () => {
    copy('https://app.platypustrade.com/register')
    setCopyStatus(true)
    const timer = setTimeout(() => {
      setCopyStatus(false)
    }, 1000)
    return () => clearTimeout(timer)
  }

  useEffect(() => {
    const formData = new FormData()
    formData.append('user_id', decoded?.userId)

    GetPromoterLink(formData).then((res: any) => {
      setLinkValue(res?.data?.Link)
    })
  }, [decoded?.userId])

  return (
    <Box className={classes.container}>
      <Grid container spacing={3}>
        <Grid item xs={12} sm={12} md={6}>
          <div className={classes.title}>Affiliate</div>
          <div className={classes.balanceTitle}>Affiliate Balance</div>
          <div className={classes.balanceShowPanel}>
            <div className={classes.firstLetter}>{balance} PLATYPUS</div>&nbsp;
            <div className={classes.secondLetter}>= {(balance * headerInfos.Price).toFixed(2)} USD</div>
          </div>
          <div className={classes.balanceTitle}>Invite Link</div>
          <div className={classes.linkShowPanel}>
            <div className={classes.linkContent}>
              https://app.platypustrade.com/register?p=ed3d2c21991e3bef5e069713af9fa6ca
            </div>
            <div className={classes.linkBtn} onClick={handleCopy}>
              <span className={classes.btnLetter}>{copyStatus ? 'Copied' : 'Copy'}</span>
            </div>
          </div>
        </Grid>
        <Grid item xs={12} sm={12} md={6}>
          <Youtube />
        </Grid>
        <Grid item xs={12}>
          <div className={classes.descriptionPanel}>
            <div className={classes.descriptionTitle}>YOU REFER, YOU EARN in PLATYPUS</div>
            <div className={classes.descriptionContent}>
              Earn as low as $20 per transaction of your referred member on Platypus when they trade or do any
              transaction. Use your unique referral link above to expand your chance of getting commission.
            </div>
            <div className={classes.descriptionContent}>
              <span className={classes.importantLetter}>Note: </span>
              There is no limit of number of referrals. There is no limit also on number of bonus and commission.
            </div>
          </div>
        </Grid>
        <Grid item xs={12}>
          <MyAffiliates />
        </Grid>
      </Grid>
    </Box>
  )
}

export default Affiliate
