import { createStyles, makeStyles, Theme } from '@material-ui/core/styles'

export const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: '#080821',
      // height: "100vh",
    },
    main: {
      margin: '40px auto 0 auto',
      padding: '0px 24px 30px 24px',
      maxWidth: '1260px',
      minHeight: 'calc(100vh - 198px)',
    },
    mainHeader: {
      marginBottom: '20px',
      alignItems: 'center',
    },
    pageTitle: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 700,
      fontSize: '30px',
      lineHeight: '35px',
      color: '#FFFFFF',
    },
    line: {
      height: '3px',
      width: '8px',
      backgroundColor: '#ffffff',
      margin: '0 8px',
    },
    coinPair: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 400,
      fontSize: '23px',
      lineHeight: '26.95px',
      color: '#FFFFFF',
      marginTop: '8px',
    },
    footerPosition: {
      borderTop: `1px solid ${theme.palette.common.white}`,
      width: '100%',
      // position: "absolute",
      // bottom: "0px",
    },
    button: {
      display: 'block',
      marginTop: theme.spacing(2),
    },
    formControl: {
      margin: theme.spacing(1),
      padding: '10px 22px 9px 20px',
      background: 'rgba(255, 255, 255, 0.07)',
      border: '1px solid rgba(255, 255, 255, 0.14)',
      borderRadius: '4px',
      display: 'flex',
      alignItems: 'center',
      flexDirection: 'row',
      '& .MuiSelect-select:focus': {
        backgroundColor: 'transparent',
      },
      '& .MuiInputBase-input': {
        fontFamily: 'Roboto',
        fontStyle: 'normal',
        fontWeight: 400,
        fontSize: '16px',
        lineHeight: '31px',
        color: '#FFFFFF',
        padding: '0 21px 0 0',
      },
    },
    selectLabel: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 400,
      fontSize: '16px',
      lineHeight: '31px',
      color: '#FFFFFF',
      opacity: 0.6,
      marginRight: '28px',
    },
  }),
)
