import { createStyles, makeStyles, Theme } from '@material-ui/core/styles'

export const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      border: `1px solid #333353`,
      backgroundColor: '#12122E',
    },
    tableTitle: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 700,
      fontSize: '18px',
      lineHeight: '126.5%',
      color: '#FFFFFF',
    },
    tableHeader: {
      display: 'flex',
      padding: '8px 20px 12px 20px',
      borderBottom: '1px solid #333353',
      minWidth: '560px',
    },
    tableHeaderCell: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 400,
      fontSize: '17px',
      lineHeight: '20px',
      color: '#A0A0AB',
      wordBreak: 'break-word',
      alignSelf: 'center',
    },
    listContainer: {
      minWidth: '600px',
      overflow: 'auto',
    },
    headerPlacement: {
      textAlign: 'right',
    },
    buySideValue: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '15px',
      lineHeight: '18px',
      color: '#26DE81',
    },
    sellSideValue: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '15px',
      lineHeight: '18px',
      color: '##EF5350',
    },
    listItem: {
      display: 'flex',
      alignItems: 'center',
      padding: '0 20px',
      '&:hover': {
        backgroundColor: `${theme.palette.secondary.contrastText}`,
      },
    },
    listItemFiled: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '15px',
      lineHeight: '18px',
      color: '#B6B9C0',
      width: '22%',
    },
    progressBar: {
      height: '32px',
      opacity: 0.15,
      animation: '0.5s ease-in-out 0s 1 normal none running animation-1rywlvk',
      cursor: 'pointer',
    },
    progressBarGreen: {
      backgroundColor: theme.palette.info.main,
    },
    progressBarRed: {
      backgroundColor: theme.palette.info.main,
    },
  }),
)
