import { withStyles, createStyles, Theme, makeStyles } from '@material-ui/core/styles'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import { InputBase } from '@material-ui/core'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      textAlign: 'center',
      padding: '2.5% 10%',
      width: '100%',
    },
    formcontrol: {
      fontSize: '14px',
      fontWeight: 500,
      margin: theme.spacing(1),
      minWidth: 120,
    },
    break: {
      [theme.breakpoints.down('xs')]: {
        marginTop: '30px !important',
        marginBottom: '15px',
      },
    },

    styledDiv: {
      maxWidth: '1400px',
      marginLeft: 'auto',
      marginRight: 'auto',
    },
    mainTitle: {
      color: 'white',
      padding: '0px 30px',
      '&.MuiTypography-h6': {
        fontFamily: 'Roboto',
        fontSize: '22px',
        fontWeight: 700,
        fontlineHeight: '25.78px',
      },
    },
    titleDescription: {
      color: theme.palette.text.secondary,
    },
    title: {
      marginBottom: theme.spacing(2),
    },
    textfieldLabel: {
      margin: theme.spacing(2, 0),
    },
    form: {
      width: '100%',
    },
    formControl: {
      width: '100%',
    },
    categories: {
      margin: '2rem 0',
    },
    symbolImg: {
      width: 42,
    },
    textEffect: {
      float: 'right',
      paddingLeft: '10px',
      margin: '5px',
      borderRadius: '5px',
      cursor: 'pointer',
      // color: "blue",
      // transition: "all 2s ease",
      // transitionProperty: "background-color, color",
      // "&:hover": {
      //   color: "#66BB6A",
      // },
      animation: `$myEffect 1000ms infinite ease`,
    },
    '@keyframes myEffect': {
      '0%': { color: '#29B6F6' },
      '50%': { color: '#EF5350' },
      '100%': {
        color: '#66BB6A',
      },
    },
    textField: {
      width: '100%',
      height: 250,
      borderRadius: 5,
      border: '1px solid lightgrey',
      overflowY: 'auto',
      '& input': {
        innerHeight: '100%',
      },
      '& input::placeholder': {
        fontSize: '8px',
      },
    },
    codeInput: {
      width: '100%',
      padding: '3.5px',
      borderRadius: 0,
      '& fieldset': {
        border: 'none',
        outerHeight: '100%',
      },
    },
    submitBtn: {
      backgroundColor: '#EF802F',
      color: 'white',
      margin: theme.spacing(2, 0),
      float: 'right',
    },
    introDiv: {
      backgroundColor: theme.palette.primary.main,
      color: theme.palette.primary.contrastText,
    },
    description: {
      color: theme.palette.text.secondary,
    },
    tabPane: {
      backgroundColor: '#12122E',
      borderRadius: '5px',
      padding: '32px 0px',
      height: '100%',
      '&.MuiPaper-outlined': {
        border: 'none',
      },
      '& .MuiBox-root': {
        padding: '0px 0px 0px',
      },
    },
    thead: {
      borderBottom: '1px solid #333353',
      borderTop: '1px solid #333353',
    },
    thtitle: {
      color: '#A0A0AB',
      padding: '18px 37px',
      fontWeight: 400,
      fontSize: '17px',
      fontFamily: 'Roboto',
      lineHeight: '19.92px',
      '&.MuiTableCell-root': {
        borderBottom: 'none',
      },
    },
    tbody: {
      borderBottom: 'none',
      fontSize: '12px',
    },
    pagination: {
      '& .MuiMenu-paper': {
        backgroundColor: 'yellow !important',
      },
      '& .MuiListItem-root.Mui-selected, .MuiListItem-root.Mui-selected:hover': {
        backgroundColor: 'green',
      },
    },
    nodata: {
      borderBottom: 'none',
      fontSize: '13px',
      marginTop: '15px',
      marginBottom: '15px',
    },
    tbOverflow: {
      borderBottom: 'none',
      fontSize: '12px',
      whiteSpace: 'nowrap',
      width: '100px',
      overflow: 'hidden',
      textOverflow: 'ellipsis',
    },
    search: {
      width: '284px',
      float: 'right',
      marginBottom: '31px',
      marginRight: '50px',
      backgroundColor: 'rgb(255 255 255 / 4%)',
      borderRadius: '4px',
      border: '1px solid #333353',
      '& .MuiInputBase-root': {
        fontSize: '18px',
        fontWeight: 400,
        lineHeight: '21.09px',
        color: 'rgb(255 255 255 / 40%)',
        fontFamily: 'Roboto',
      },
    },
    cdeposit: {
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'space-between',
      height: '100%',
      backgroundColor: 'transparent',
      fontWeight: 500,
      borderRadius: '7px',
      '&.MuiPaper-outlined': {
        border: 'none',
      },
    },
    cdephis: {
      backgroundColor: '#12122E !important',
      color: theme.palette.primary.contrastText,
      fontWeight: 500,
      marginTop: '30px',
      borderRadius: '7px',
      // padding: '20px',
      '&.MuiPaper-elevation1': {
        bosShadow: 'none',
      },
    },
    cbalance: {
      backgroundColor: '#F0BA03',
      padding: '30px 10px',
      textAlign: 'center',
      borderRadius: '5px',
      [theme.breakpoints.down('xs')]: {
        marginTop: '15px !important',
        marginBottom: '15px',
      },
    },
    CDletter: {
      fontFamily: 'Roboto',
      fontWeight: 700,
      fontSize: '22px',
      lineHeight: '25.78px',
      textAlign: 'left',
      color: 'white',
    },
    balanceNumber: {
      fontFamily: 'Roboto',
      fontWeight: 500,
      fontSize: '27px',
      lineHeight: '31.64px',
      textAlign: 'center',
      color: '#12122E',
      marginBottom: '8px',
    },
    balanceLetter: {
      fontFamily: 'Roboto',
      fontWeight: 500,
      fontSize: '19px',
      lineHeight: '14px',
      textAlign: 'center',
      color: '#12122E',
    },
    caddressContainer: {
      padding: '0px 30px',
    },
    caddress: {
      backgroundColor: 'rgb(255 255 255 / 7%)',
      marginTop: '30px',
      color: '#333333',
      padding: '33px 58px 33px 39px',
      lineHeight: 1.5,
      fontSize: '25px',
      fontWeight: 400,
      borderRadius: '4px',
    },
    cbalgrd: {
      paddingLeft: '30px',
      [theme.breakpoints.down('xs')]: {
        paddingLeft: '0px',
      },
    },
    paper: {
      backgroundColor: theme.palette.primary.main,
      color: theme.palette.primary.contrastText,
      borderRadius: '7px',
    },
    error: {
      color: 'red',
      marginLeft: 0,
    },
    root1: {
      background: '#080821',
      height: 'calc(100vh -60px)',
      '@media (min-width: 550px)': {
        minHeight: '100vh',
      },
      width: '100%',
    },
    link: {
      textDecoration: 'none',
      padding: '6px 12px',
      width: 'max-content',
      height: 'max-content',
      alignSelf: 'center',
    },
    hover: {
      cursor: 'pointer',
      '&:hover': {
        color: theme.palette.text.disabled,
      },
    },
    appbar: {
      background: '#181a20',
      '&.MuiPaper-elevation4': {
        boxShadow: 'none',
      },
    },
    toolbar: {
      justifyContent: 'space-between',
      display: 'flex',
      '&.MuiToolbar-gutters': {
        padding: '0 24px 0 16px',
        [theme.breakpoints.down(760)]: {
          padding: '0 0 0 8px',
        },
      },
    },
    fontColor1: {
      color: '#fff',
      marginLeft: '0px',
    },
    button: {
      backgroundColor: '#4CAF50',
      fontSize: '12px',
      textTransform: 'none',
      color: '#fff',
      '&:hover': {
        backgroundColor: '#4CAF70',
      },
    },
    draw: {
      height: '48px',
      marginTop: '15px',
      backgroundColor: '#F0BA03',
      fontSize: '17px',
      fontWeight: 500,
      lineHeight: '20px',
      textTransform: 'none',
      color: '#161A1E',
      fontFamily: 'Roboto',
      '&:hover': {
        // backgroundColor: '#028BFF',
      },
    },
    tableRoot: {
      marginTop: '50px',
    },
    buy: {
      cursor: 'pointer',
      margin: '0px',
      padding: '8px 24px',
      fontSize: '16px',
      textTransform: 'none',
      color: theme.palette.secondary.main,
      fontWeight: 500,
      '&:hover': {
        color: theme.palette.secondary.dark,
      },
    },
    opt: {
      backgroundColor: '#026BFB',
      fontSize: '12px',
      textTransform: 'none',
      color: '#fff',
      height: 'fit-content',
      width: '82px',
      marginTop: 'auto',
      marginBottom: 'auto',
      marginLeft: '20px',
      lineHeight: 'normal',
      '&:hover': {
        backgroundColor: '#028BFF',
      },
    },

    textfld: {
      '& label.Mui-focused': {
        color: theme.palette.secondary.main,
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: '#333353',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: '#333353',
        },
        '&:hover fieldset': {
          borderColor: '#333353',
        },
        '&.Mui-focused fieldset': {
          borderColor: '#333353',
        },
      },
    },
    fiatWithDrawContainer: {
      padding: '60px 0px',
    },
    inputsContainer: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      marginBottom: '25px',
    },
    cryptoWithUppartContainer: {
      padding: '60px 30px 50px',
    },
    inputContainer: {
      width: '100%',
    },
    payment: {
      paddingTop: '15px',
      '& label.Mui-focused': {
        color: 'white',
      },
    },
    walletxt: {
      color: theme.palette.secondary.dark,
      fontSize: '24px',
      fontWeight: 600,
      padding: '16px',
    },
    container: {
      // marginLeft: '5%',
      // marginRight: '5%',
      backgroundColor: '#080821',
    },
    themeIcon: {
      marginTop: '3px',
      fill: '#fff',
      '&:hover': {
        fill: '#c99400',
      },
    },
    backDiv: {
      display: 'flex',
      textDecoration: 'none',
      marginRight: '15px',
      lineHeight: '24px',
      fontSize: '14px',
      color: '#e5ecf3',
      marginTop: '3px',
      '&:hover': {
        color: '#f0ba03',
      },
    },
    back: {
      color: '#eaecef',
      fontSize: '14px',
      fontWeight: 500,
      textDecoration: 'none',
      marginTop: '5px',
      '&:hover': {
        color: '#f0b90b',
      },
    },
    alert: {
      fontFamily: 'Roboto',
      color: '#F0BA03',
      fontSize: '15px',
      lineHeight: '17.58px',
      fontWeight: 400,
      background: 'transparent',
      float: 'right',
    },
    tabDiv: {
      display: 'flex',
      justifyContent: 'space-between',
      alignSelf: 'flex-start',
      alignItems: 'center',
      width: '100%',
      // textAlign: 'left',
    },
    backIcon: {
      fill: '#eaecef',
      marginRight: '0px',
      marginTop: '4px',
      verticalAlign: 'bottom',
      width: '20px',
      '&:hover': {
        fill: 'green',
      },
    },
    cryptoDepositContainer: {
      padding: '32px 0px',
      borderRadius: '#12122E',
      backgroundColor: '#12122E',
    },
    CDepositUpContainer: {
      padding: '0px 30px',
    },
    CDepositSelectContainer: {
      backgroundColor: 'rgb(255 255 255 / 7%)',
      borderRadius: '4px',
      border: '1px solid #333353',
      color: 'white',
      fontFamily: 'Roboto',
      fontWeight: 500,
      fontSize: '18px',
      lineHeight: '31px',
      '& .MuiSelect-selectMenu': {
        padding: '10px 21px',
      },
    },
  }),
)

export default useStyles

export const StyledVerticalTabs = withStyles((theme: Theme) => ({
  root: {
    borderRadius: '5px',
    width: '100%',
  },
  indicator: {
    background: 'none',
  },
}))(Tabs)

export const StyledVerticalTab = withStyles((theme: Theme) => ({
  root: {
    fontFamily: 'Roboto',
    textTransform: 'none',
    fontSize: '18px',
    lineHeight: '21.09px',
    fontWeight: 500,
    maxWidth: '100%',
    minHeight: 42,
    color: '#A3A3A9',
    padding: '22px 25px',
    borderBottom: '1px solid #333353',
    '&:hover': {
      backgroundColor: 'rgb(255 255 255 / 7%)',
      color: 'white',

      //   `${
      //   localStorage.appTheme === "darkTheme" ? "#422d1d" : "#fde0cb"
      // }`,
    },
    '&$selected': {
      color: 'white',
      backgroundColor: 'rgb(255 255 255 / 7%)',
      //   `${
      //   localStorage.appTheme === "darkTheme" ? "#EF802F" : "#EF802F"
      // }`,
    },
    '&:focus': {},
  },
  selected: {},
}))(Tab)

export const StyledOutlineInput = withStyles((theme) => ({
  root: {
    border: `${localStorage.appTheme === 'darkTheme' ? '1px solid #474d57' : '1px solid #eaecef'}`,
    borderRadius: '4px',
    width: '100%',
    color: theme.palette.secondary.dark,
    '&:hover': {
      border: '1px solid #f0b90b',
    },
    padding: '0.3rem 0.5rem',
  },
  input: {
    background: 'transparent',
    // border: "none",
    '&:-internal-autofill-selected': {
      background: 'transparent !important',
    },
  },
}))(InputBase)
