import { Link } from 'react-router-dom'
import { Box } from '@material-ui/core'
import { useStyles } from './Style'
import RoundBackBtn from 'components/Base/RoundBackBtn'

interface SecurityCardProps {
  isEnable: boolean
  linkTitle: string
  url: string
}

function SecurityCard({ isEnable, linkTitle, url }: SecurityCardProps) {
  const classes = useStyles()

  return (
    <Box display="flex" alignItems="center">
      {isEnable ? (
        <i
          className="icon-success"
          style={{
            lineHeight: '21px',
            fontSize: '21px',
            border: 'solid 1px rgba(255, 255, 255, 0.18)',
            borderRadius: '66px',
          }}
        >
          <i className="path1 fontStyle" />
          <i className="path2 fontStyle" />
        </i>
      ) : isEnable === undefined ? (
        ''
      ) : (
        <RoundBackBtn type="multiply" className={classes.closeIcon} />
      )}
      <Link to={url} className={classes.securityHeaderLink}>
        {linkTitle}
      </Link>
    </Box>
  )
}

export default SecurityCard
