import ActionButton from 'components/Dashboard/ActionButton'
import clsx from 'clsx'
import { Link } from 'react-router-dom'
//----------------style-----------
import { useStyles } from './Style'

export default function WalletDirect() {
  const classes = useStyles()

  return (
    <div className={classes.root}>
      <div className={classes.walletDes}>
        <i className={clsx('icon-wallet-direction', classes.icon)}></i>
        <span className={classes.text}>
          Transfer coins or tokens between Platypus.com account and Platypus Chain Wallet.
        </span>
      </div>
      <Link to="/wallet" className={classes.link}>
        <ActionButton type="yellow" className={classes.linkBtn}>
          Wallet Direction
        </ActionButton>
      </Link>
    </div>
  )
}
