import { createStyles, makeStyles, Theme } from '@material-ui/core/styles'

export const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      paddingTop: '32px',
      flexGrow: 1,
    },
    pageTitle: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 700,
      fontSize: '25px',
      lineHeight: '29px',
      padding: '10px 0',
      color: '#FFFFFF',
    },
    paymentHeader: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      marginBottom: '33px',
      padding: '0 27px 0 43px',
    },
    tabContainer: {
      background: '#12122E',
      height: '51px',
      width: '100%',
      display: 'flex',
    },
    tabItem: {
      cursor: 'pointer',
      padding: '0 24px',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      marginRight: '10px',
      borderBottom: 'solid 2px transparent',
    },
    tabItemName: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '20px',
      lineHeight: '23px',
      textAlign: 'center',
      color: '#FFFFFF',
      opacity: 0.8,
      '&:hover': {
        opacity: 1,
      },
      [theme.breakpoints.down(767)]: {
        fontSize: '16px',
      },
    },
    activeTabItem: {
      borderBottom: 'solid 2px #F0BA03',
      '& p': {
        opacity: 1,
      },
    },
    displayNone: {
      display: 'none',
    },
  }),
)
