import { createStyles, makeStyles, Theme } from '@material-ui/core/styles'

export const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      flexGrow: 1,
      backgroundColor: '#080821',
    },
    header: {
      backgroundColor: 'transparent',
      padding: '24px',
      boxShadow: 'rgb(0 0 0 / 5%) 0px 0px 4px',
      display: 'flex',
      justifyContent: 'center',
    },
    avatar: {
      backgroundColor: '#12122E',
      border: '1px solid rgba(255, 255, 255, 0.14)',
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 700,
      fontSize: '25px',
      lineHeight: '29px',
      color: '#FFFFFF',
      [theme.breakpoints.up(767)]: {
        width: '69px',
        height: '69px',
      },
    },
    logedUserName: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '22px',
      lineHeight: '26px',
      color: '#FFFFFF',
      opacity: 0.9,
      marginRight: '5px',
    },
    userId: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '18px',
      lineHeight: '31px',
      color: '#B5B5BC',
      marginRight: '16px',
      marginLeft: '13px',
    },
    userIdNumber: {
      color: '#ffffff',
    },
    vipLink: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '18px',
      lineHeight: '31px',
      color: '#f0ba03',
      display: 'flex',
      alignItems: 'center',
      textDecorationLine: 'underline',
      '&:hover': {
        color: '#f0b90b',
      },
    },
    verifyLink: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '18px',
      lineHeight: '31px',
      color: '#f0ba03',
      marginLeft: '5px',
      textDecorationLine: 'underline',
      '&:hover': {
        color: '#f0b90b',
      },
    },
    linkedLinkParagraph: {
      marginLeft: '16px',
      display: 'flex',
      alignItems: 'center',
      lineHeight: 1,
      background: '#202037',
      borderRadius: '4px',
      padding: '8px',
      '& i': {
        color: '#B5B5BC',
        marginRight: '8px',
      },
      '& span': {
        color: '#B5B5BC',
        textDecoration: 'none',
      },
    },
    userLoginInfo: {
      display: 'flex',
      flexWrap: 'wrap',
      alignItems: 'center',
      '& p': {
        fontFamily: 'Roboto',
        fontStyle: 'normal',
        fontWeight: 500,
        fontSize: '18px',
        lineHeight: '31px',
        color: '#B5B5BC',
      },
      '& div': {
        background: '#B5B5BC',
        height: '12px',
      },
      [theme.breakpoints.down(767)]: {
        marginTop: '16px',
      },
    },
    userInfoContainer: {
      display: 'flex',
      marginBottom: '4px',
      flexDirection: 'row',
      [theme.breakpoints.down(767)]: {
        flexDirection: 'column',
      },
    },
    userInfo: {
      display: 'flex',
      alignItems: 'center',
      flexWrap: 'wrap',
      [theme.breakpoints.down(767)]: {
        marginBottom: '4px',
      },
    },
    notLinkedConnect: {
      marginLeft: '8px',
      color: '#f0b90b',
      textDecoration: 'underline',
      cursor: 'pointer',
    },
    notLinkedTooltip: {
      color: '#ffffff',
      backgroundColor: '#5e6673',
      borderRadius: '4px',
      padding: '8px 12px',
      '& p': {
        fontWeight: 400,
        fontSize: '12px',
        lineHeight: '16px',
      },
    },
    notLinkedTooltipArrow: {
      color: '#5e6673',
    },
    divLine: {
      width: '2px',
      height: '14px',
      background: '#ffffff',
    },
    cardsRoot: {
      padding: '15px',
    },
    cardRoot: {
      display: 'flex',
      padding: '15px 10px',
    },
    columnSetting: {
      flexDirection: 'column',
      justifyContent: 'space-between',
    },
    increaseRoot: {
      flexGrow: 1,
      marginBottom: '20px',
    },
  }),
)
