export const PercentSlider = withStyles((theme: Theme) =>
  createStyles({
    root: {
      color: '#b7bdc6',
      height: '6px',
      padding: '15px 0',
      width: '90%',
      marginLeft: '5%',
    },
    thumb: {
      height: 11,
      width: 11,
      background: '#7C7C89',
      border: '3px solid #7C7C89',
      borderRadius: '1px',
      marginTop: '-3px',
      '&:focus, &:hover, &$active': {
        boxShadow: '0 3px 1px rgba(0,0,0,0.1),0 4px 8px rgba(0,0,0,0.3),0 0 0 1px rgba(0,0,0,0.02)',
        // Reset on touch devices, it doesn't add specificity
        '@media (hover: none)': {},
      },
    },
    active: {},
    valueLabel: {
      left: 'calc(-50% + 12px)',
      top: -22,
      '& *': {
        background: 'transparent',
        color: '#000',
      },
    },
    track: {
      height: '4px',
      background: '#7C7C89',
      borderRadius: '44px',
    },
    rail: {
      height: '4px',
      borderRadius: '44px',
      backgroundColor: '#3C3C50',
    },
    mark: {
      '&.MuiSlider-markLabel': {
        color: 'red',
      },
      height: 11,
      width: 11,
      marginLeft: '-6px',
      marginTop: -3,
      background: '#080821',
      border: '2px solid #3C3C50',
      borderRadius: '1px',
      '&:hover': {
        backgroundColor: `${localStorage.appTheme === 'darkTheme' ? '#474d57' : '#b7bdc6'}`,
        border: '2px solid' + theme.palette.info.light,
      },
    },
    markActive: {
      backgroundColor: '#7C7C89',
      border: '2px solid #080821',
      borderRadius: '1px',
    },
    markLabel: {
      marginTop: '12px',
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 400,
      fontSize: '15px',
      lineHeight: '18px',
      color: '#A1A1A1',
    },
    markLabelActive: {
      color: theme.palette.secondary.dark,
    },
  }),
)(Slider)

const Darktooltip = makeStyles(() => ({
  arrow: {
    color: '#12161c',
  },
  tooltip: {
    maxWidth: 250,
    backgroundColor: '#12161c',
    color: 'white',
    textAlign: 'center',
    padding: '4px 8px',
    borderRadius: '6px',
    fontSize: '11px',
  },
}))

export function StyledTooltip(props: TooltipProps) {
  const classes = Darktooltip()

  return <Tooltip arrow classes={classes} {...props} />
}
