import React from 'react'
import { Box, Typography } from '@material-ui/core'
import { useStyles } from './Style'
import clsx from 'clsx'
import RoundBackBtn from 'components/Base/RoundBackBtn'

interface SecurityCardProps {
  icon: React.ReactNode
  title: string
  description: React.ReactNode
  isEnable?: boolean
  statusValue?: string
  controlButtons: React.ReactNode
  className?: any
}

function SecurityCard({
  icon,
  title,
  description,
  isEnable,
  statusValue,
  controlButtons,
  className,
}: SecurityCardProps) {
  const classes = useStyles()
  return (
    <Box className={clsx(classes.securityCardContainer, className)}>
      <Box className={classes.securityCardInfo}>
        <Box marginRight="44.57px">{icon}</Box>
        <Box>
          <Typography className={classes.securityCardTitle}>{title}</Typography>
          {description}
        </Box>
      </Box>
      <Box className={classes.securityCardAction}>
        <Box display="flex" flex="2 1 0%" alignItems="center" mr="20px">
          {isEnable ? (
            <i
              className="icon-success"
              style={{
                lineHeight: '21px',
                fontSize: '21px',
                border: 'solid 1px rgba(255, 255, 255, 0.18)',
                borderRadius: '66px',
              }}
            >
              <i className="path1 fontStyle" />
              <i className="path2 fontStyle" />
            </i>
          ) : isEnable === undefined ? (
            ''
          ) : (
            <RoundBackBtn type="multiply" className={classes.closeIcon} />
          )}
          <Typography className={classes.securityCardStatusText}>{statusValue}</Typography>
        </Box>
        <Box display="flex" flex="3 1 0%" justifyContent="flex-end" className={classes.securityBtnRoot}>
          {controlButtons}
        </Box>
      </Box>
    </Box>
  )
}

export default SecurityCard
