import React from 'react'
//material-ui
import { createStyles, Theme, withStyles, WithStyles } from '@material-ui/core/styles'
import { Typography, IconButton, Dialog, Box, Tabs, Tab } from '@material-ui/core'
import MuiDialogTitle from '@material-ui/core/DialogTitle'
import MuiDialogContent from '@material-ui/core/DialogContent'
//external
import { useStyles } from './Style'
import RoundBackBtn from 'components/Base/RoundBackBtn'
import EnFlagIcon from './../../../assets/image/country_flag/England.png'
import CNFlagIcon from './../../../assets/image/country_flag/China.png'
import RUFlagIcon from './../../../assets/image/country_flag/Russia.png'

import GMFlagIcon from './../../../assets/image/country_flag/Germany.png'

import FRFlagIcon from './../../../assets/image/country_flag/France.png'

import SPFlagIcon from './../../../assets/image/country_flag/Spain.png'

const styles = (theme: Theme) =>
  createStyles({
    root: {
      margin: 0,
      padding: '0px 6px',
      borderBottom: '1px solid  #333353',
    },
    closeButton: {
      position: 'absolute',
      right: '30px',
      top: '20px',
      width: '35px',
      height: '35px',
      color: theme.palette.grey[500],
    },
  })

export interface DialogTitleProps extends WithStyles<typeof styles> {
  id: string
  children: React.ReactNode
  onClose: () => void
}

const DialogTitle = withStyles(styles)((props: DialogTitleProps) => {
  const { children, classes, onClose, ...other } = props
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      {children}
      {onClose ? <RoundBackBtn onClick={onClose} type="multiply" className={classes.closeButton} /> : null}
    </MuiDialogTitle>
  )
})

const DialogContent = withStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(3),
  },
}))(MuiDialogContent)

interface TabPanelProps {
  children?: React.ReactNode
  index: any
  value: any
}

const AntTabs = withStyles((theme) => ({
  root: {
    minHeight: '64px',
  },
  indicator: {
    backgroundColor: theme.palette.text.disabled,
    height: '1px',
  },
}))(Tabs)

interface StyledTabProps {
  label: string
}

const AntTab = withStyles((theme: Theme) =>
  createStyles({
    root: {
      fontFamily: 'Roboto',
      fontWeight: 700,
      lineHeight: '23.44px',
      textAlign: 'center',
      textTransform: 'none',
      padding: '27px 22px',
      minWidth: 72,
      marginRight: theme.spacing(4),
      borderBottom: '#F0BA03 !important',
      // color: theme.palette.secondary.dark,
      color: 'white',
      // minHeight: '64px',
      fontSize: '20px',
      '@media (max-width:700px)': {
        marginRight: '5px',
      },
    },
    selected: {},
  }),
)((props: StyledTabProps) => <Tab disableRipple {...props} />)

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box>{children}</Box>}
    </div>
  )
}

function a11yProps(index: any) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  }
}

interface LanguageCurrencySelectDialogProps {
  open: boolean
  value: number
  setOpen: (isOpen: boolean) => void
  // setLanguage: (language: string) => void
  // setCurrency: (currency: string) => void
  setValue: (value: number) => void
}

export default function LanguageCurrencySelectDialog({
  open,
  value,
  setOpen,
  // setLanguage,
  // setCurrency,
  setValue,
}: LanguageCurrencySelectDialogProps) {
  const classes = useStyles()

  const handleClose = () => {
    setOpen(false)
  }

  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue)
  }

  const languageArray = ['Chinese', 'Russia', 'Germany', 'France', 'Spain']

  const currencyArray = [
    { id: 1, currency: 'AUD', symbol: 'A$' },
    { id: 2, currency: 'IDR', symbol: 'Rp' },
    { id: 3, currency: 'HKD', symbol: 'HKD' },
    { id: 4, currency: 'CAD', symbol: 'C$' },
    { id: 5, currency: 'KRK', symbol: 'kn' },
  ]

  // const [language, setLanguage] = React.useState(initLanguage);
  // const [currency, setCurrency] = React.useState(initCurrency);

  const handleLanguageChange = (language: string) => {
    // localStorage.setItem("activeLanguage", language);
    // setLanguage(language);
    handleClose()
  }

  const handleCurrencyChange = (currency: string) => {
    // localStorage.setItem("activeCurrency", currency);
    // setCurrency(currency);
    handleClose()
  }

  return (
    <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open} className={classes.dialog}>
      <DialogTitle id="customized-dialog-title" onClose={handleClose}>
        <AntTabs
          value={value}
          className={classes.tabBtnContainer}
          onChange={handleChange}
          aria-label="simple tabs example"
        >
          <AntTab label="Language and Region" {...a11yProps(0)} />
          <AntTab label="Currency" {...a11yProps(1)} />
        </AntTabs>
      </DialogTitle>
      <DialogContent className={classes.chooseDialogRoot}>
        <TabPanel value={value} index={0}>
          <Typography className={classes.chooseTitle}>Choose a language and region</Typography>
          <Box className={classes.countrySelectContainer}>
            <div className={classes.languageSelectButton} onClick={() => handleLanguageChange('English')}>
              <div className={classes.flagIconContainer}>
                <img alt="UK" src={EnFlagIcon} className={classes.flagIcon} />
              </div>
              <span className={classes.countryLetter}>English</span>
            </div>
            {languageArray.map((language: string) => (
              <div
                key={language}
                className={classes.languageSelectButton1}
                // onClick={() => handleLanguageChange(language)}
              >
                <div className={classes.flagIconContainer}>
                  {language === 'Chinese' ? (
                    <img alt="flg" src={CNFlagIcon} className={classes.flagIcon} />
                  ) : language === 'Russia' ? (
                    <img alt="flg" src={RUFlagIcon} className={classes.flagIcon} />
                  ) : language === 'Germany' ? (
                    <img alt="flg" src={GMFlagIcon} className={classes.flagIcon} />
                  ) : language === 'France' ? (
                    <img alt="flg" src={FRFlagIcon} className={classes.flagIcon} />
                  ) : (
                    <img alt="flg" src={SPFlagIcon} className={classes.flagIcon} />
                  )}
                </div>
                <div>{language}</div>
              </div>
            ))}
          </Box>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <Typography className={classes.chooseTitle}>Choose a currency</Typography>
          <Box className={classes.countrySelectContainer}>
            <div className={classes.languageSelectButton} onClick={() => handleCurrencyChange('USD - $')}>
              <span className={classes.countryLetter}>USD - </span>
              <span className={classes.goldenDollar}>$</span>
            </div>
            {currencyArray.map((currency) => (
              <Typography
                key={currency.id}
                variant="body2"
                className={classes.languageSelectButton1}
                // onClick={() => handleCurrencyChange(currency.currency)}
              >
                {currency.currency} - {currency.symbol}
              </Typography>
            ))}
          </Box>
        </TabPanel>
      </DialogContent>
    </Dialog>
  )
}
