//material-ui
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles'
//style
export const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    CustomCard: {
      background: '#080821',
      border: '1px solid #333353',
      borderRadius: '6px',
      width: '100%',
      boxShadow: 'rgb(20 21 26 / 4%) 0px 1px 2px, rgb(71 77 87 / 4%) 0px 3px 6px, rgb(20 21 26 / 10%) 0px 0px 1px',
      boxSizing: 'border-box',
    },
    CustomCardHeader: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      background: '#12122E',
      borderRadius: '6px 6px 0 0',
      paddingRight: '23px',
    },
    CustomCardHeaderTitle: {
      display: 'flex',
      boxSizing: 'border-box',
      minWidth: '0px',
      padding: '0px',
      marginRight: '16px',
      flex: '1 1 0%',
    },
    CustomCardHeaderTitleA: {
      textDecoration: 'none',
      display: 'flex',
      color: '#000000',
      fontWeight: 600,
      fontSize: '16px',
      '&:hover': {
        color: '#f1bb12',
      },
    },
    CustomCardHeaderDescription: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
    },
    CustomCardHeaderTitleIcon: {
      display: 'flex',
      color: '#ffffff',
      marginLeft: '10px',
      width: '10px',
      height: '12px',
      cursor: 'pointer',
      '&:hover': {
        color: '#f1bb12',
      },
    },
    tabItem: {
      padding: '20px 20px 18px 20px',
      cursor: 'pointer',
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 600,
      fontSize: '20px',
      lineHeight: '23px',
      color: '#B5B5BC',
      '&:hover': {
        color: '#ffffff',
      },
    },
    activeItem: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 600,
      fontSize: '20px',
      lineHeight: '23px',
      color: '#FFFFFF',
      borderBottom: '2px solid #F0BA03',
    },
    disableAccount: {
      textDecoration: 'underline',
      color: '#c99400',
      fontSize: '12px',
      fontWeight: 400,
      padding: '20px 0 18px 0',
      '&:hover': {
        color: '#f0b90b',
      },
    },
    firstItem: {
      padding: '14px 20px',
      lineHeight: 1.5,
    },
    activityItem: {
      borderTop: '1px solid #333353',
      padding: '14px 20px',
      lineHeight: 1.5,
    },
    itemBrowser: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '18px',
      lineHeight: '21px',
      color: '#FFFFFF',
    },
    itemIp: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 400,
      fontSize: '18px',
      lineHeight: '21px',
      textAlign: 'right',
      color: '#FFFFFF',
    },
    itemSystem: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 400,
      fontSize: '18px',
      lineHeight: '31px',
      color: '#B5B5BC',
    },
    itemDate: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 400,
      fontSize: '18px',
      lineHeight: '31px',
      textAlign: 'right',
      color: '#B5B5BC',
    },
  }),
)
