import { createStyles, Theme } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'

export const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: '#32281d',
      color: '#ffffff',
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 400,
      fontSize: '15px',
      lineHeight: '21px',
      width: '100%',
      marginBottom: '24px',
      padding: '8px 12px 8px 12px',
      '& .MuiAlert-message': {
        display: 'flex',
        padding: '0',
      },
    },
    alertcontext: {
      fontSize: '14px',
      fontFamily: 'BinancePlex,Arial,sans-serif!important',
    },
    alert_icon: {
      fontSize: '20px',
      marginRight: '8px',
      '& i': {
        color: '#F0B90B',
      },
    },
  }),
)
