import React, { useState } from 'react'
import { makeStyles, Theme } from '@material-ui/core/styles'
import Box from '@material-ui/core/Box'
import Grid from '@material-ui/core/Grid'

import { StyledVerticalTabs, StyledVerticalTab } from './styles'

interface TabPanelProps {
  children?: React.ReactNode
  index: any
  value: any
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      style={{ alignItems: 'left', height: '100%' }}
      {...other}
    >
      {value === index && (
        <Box pl={0} pt={0} style={{ height: '100%' }}>
          {children}
        </Box>
      )}
    </div>
  )
}

function a11yProps(index: any) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  }
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    flexGrow: 1,
    '& .MuiGrid-spacing-xs-2': {
      margin: '0px',
      width: '100%',
      height: 'calc(100vh - 81px)',
      [theme.breakpoints.down('md')]: {
        height: '100%',
      },
    },
  },
  tabs: {
    backgroundColor: '#12122E',
    color: theme.palette.primary.contrastText,
    textAlign: 'left',
  },
  tabpane: {
    backgroundColor: theme.palette.primary.main,
  },
  leftBarContainer: {
    // minWidth: '473px',
    '&.MuiGrid-item': {
      padding: '28px 34px 0px 12px',
      borderRight: '1px solid #333353',
      [theme.breakpoints.down('md')]: {
        padding: '28px 30px 00px 30px',
      },
    },
  },
  mainContainer: {
    '&.MuiGrid-item': {
      padding: '28px 30px',
      height: '100%',
    },
  },
  financialLetter: {
    fontWeight: 500,
    fontFamily: 'Roboto',
    fontSize: '22px',
    lineHeigth: '25.78px',
    color: 'white',
    marginBottom: '20px',
  },
}))

interface TabProps {
  tabs: {
    id: any
    children?: React.ReactNode
    label: React.ReactNode
    index: any
  }[]
}

const ResponsiveVerticalTabs: React.FC<TabProps> = ({ tabs }) => {
  const classes = useStyles()
  const [value, setValue] = useState(0)

  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    // if (newValue !== 2 && newValue !== 4) {
    setValue(newValue)
    // }
  }

  var TabHeaders = tabs.map((tab, index) => (
    <StyledVerticalTab wrapped key={tab.id} label={tab['label']} {...a11yProps(tab['index'])} />
  ))

  var TabContents = tabs.map((tab, index) => (
    <TabPanel key={tab.id} value={value} index={tab['index']}>
      {tab['children']}
    </TabPanel>
  ))

  return (
    <div className={classes.root}>
      <div>
        <Grid container spacing={2}>
          <Grid item xs={12} md={3} className={classes.leftBarContainer}>
            <div className={classes.financialLetter}>Financial Center11</div>
            <StyledVerticalTabs
              orientation="vertical"
              variant="scrollable"
              value={value}
              onChange={handleChange}
              aria-label="Vertical tabs example"
              className={classes.tabs}
            >
              {TabHeaders}
            </StyledVerticalTabs>
          </Grid>
          <Grid item xs={12} md={9} className={classes.mainContainer}>
            {TabContents}
          </Grid>
        </Grid>
      </div>
    </div>
  )
}

export default ResponsiveVerticalTabs
