import { createStyles, makeStyles, Theme } from '@material-ui/core/styles'
import leftArrow from '../../assets/image/leftarrow_icon.svg'
import multipylIcon from '../../assets/image/multiply_icon.svg'
import clsx from 'clsx'
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      cursor: 'pointer',
      minWidth: '30px',
      width: '30px',
      height: '30px',
      justifyContent: 'center',
      display: 'flex',
      alignItems: 'center',
      background: 'rgba(255, 255, 255, 0.1)',
      opacity: 0.9,
      border: '1px solid rgba(255, 255, 255, 0.18)',
      borderRadius: '66px',
      '& i': {
        fontSize: '10px',
        fontWeight: 100,
      },
    },
  }),
)

interface Props {
  className?: any
  onClick?: () => void
  type?: string
}

export default function RoundBackBtn({ className, onClick, type }: Props) {
  const classes = useStyles()

  return (
    <div className={clsx(classes.root, className)} onClick={onClick}>
      {type === 'multiply' ? (
        <img src={multipylIcon} alt="multiplyicon" />
      ) : (
        <img src={leftArrow} alt="leftarrowicon" />
      )}
    </div>
  )
}
