import { Box, Grid } from '@material-ui/core'
import { useStyles } from './style'
import Header from './components/Header'
import BalanceDetail from './BalanceDetail'
import Announcements from './Announcements'
import TaskCenter from './TaskCenter'
import WalletDirect from './WalletDirect'
import ActivityDevices from './ActivityDevices'
import IncreaseYourAccountSecurity from './IncreaseYourAccountSecurity'
import DashboradApiCard from './DashboradApiCard'
import Distribution from './Distribution'
import OpenOrders from './OpenOrders'
import YourTradingFeeLevel from './YourTradingFeeLevel'
import Stepper from './Stepper'
import clsx from 'clsx'

function Dashboard() {
  const classes = useStyles()

  return (
    <Box className={classes.container}>
      <Header />
      <Stepper />
      <BalanceDetail />
      <WalletDirect />
      <Grid container className={classes.cardsRoot}>
        <Grid item xs={12} sm={12} md={6} className={classes.cardRoot}>
          <ActivityDevices />
        </Grid>
        <Grid item xs={12} sm={12} md={6} className={clsx(classes.cardRoot, classes.columnSetting)}>
          <IncreaseYourAccountSecurity className={classes.increaseRoot} />
          <DashboradApiCard />
        </Grid>
        <Grid item xs={12} sm={12} md={6} className={classes.cardRoot}>
          <OpenOrders />
        </Grid>
        <Grid item xs={12} sm={12} md={6} className={classes.cardRoot}>
          <YourTradingFeeLevel />
        </Grid>
        <Grid item xs={12} sm={12} md={4} className={classes.cardRoot}>
          <Announcements />
        </Grid>
        <Grid item xs={12} sm={12} md={4} className={classes.cardRoot}>
          <TaskCenter />
        </Grid>
        <Grid item xs={12} sm={12} md={4} className={classes.cardRoot}>
          <Distribution />
        </Grid>
      </Grid>
    </Box>
  )
}

export default Dashboard
